body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  font-family: "Barlow Condensed", sans-serif;
  color: white;
  background-color: rgb(255, 136, 128);
}

.bioDiv {
  padding-left: 30px;
  padding-right: 0;
}

.col-md-3 {
  padding: 0;
}

.col-md-9 {
  padding-right: 0;
}

.name {
  font-size: 17rem;
  text-shadow: 5px 5px rgba(0, 0, 0, 0.1);
  padding-left: 10px;
}

.role {
  margin-top: -50px;
  font-size: 6rem;
  text-shadow: 4px 4px rgba(0, 0, 0, 0.1);
  padding-left: 20px;
  letter-spacing: 10px;
}

.bio {
  font-size: 1.25rem;
  text-align: justify;
  font-family: 'Barlow', sans-serif;
  padding-left: 30px;
  padding-right: 30px;
}

.infoRow {
  margin-top: 55px;
}

.profileImg {
  height: 300px;
  padding-right: 25px;
  margin-top: 85px;
}

.imgDiv {
  text-align: right;
  padding-right: 35px;
  margin-bottom: 0;


}

.image {
  height: 325px;
  padding-top: 55px;
  margin-bottom: 0;
}

.techSkillDiv {
  font-size: 1.5rem;
  font-family: 'Barlow', sans-serif;
  border: 1px solid rgba(255, 255, 255, 0.5);
  margin: 0;
  padding: 0;
  border-radius: 2px;   
}

.link {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px rgba(255, 255, 255, 0) solid;
  border-radius: 2px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 2rem;
  margin-top: 5px;
  text-align: center;
  letter-spacing: 2px;

}

.link:hover {
  background-color: rgba(255, 255, 255, 0);
  border-radius: 2px;
  cursor: pointer;
  border: 1px rgba(255, 255, 255, 0.2) solid;
}

ul {
  list-style-type: none;
  padding: 0;
}

hr {
  border-color: rgba(255, 255, 255, 0.5);
  padding: 0;
  margin: 0;
}

.techs {
  font-family: 'Barlow', sans-serif
}

.techHeader {
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 2rem;
  padding-left: 15px;
  padding-top: 5px;
  letter-spacing: 2px;
}

.techDiv {
  border: 1px rgb(255, 255, 255, 0.5) solid;
  border-radius: 2px;
  margin-top: 7px;
  padding-top: 15px;
  padding-bottom: 5px;
}

.techHeaderDiv {
  padding-left: 0;
  padding-right: 0;
  background-color: rgba(255, 255, 255, 0.1);
  margin-top: 5px;
  border-radius: 2px;
  margin-bottom: 15px;
}

.borderless td {
  border: none;
  font-size: 1.25rem;
  padding: 0;
}


